<template>
  <div class="column is-8 is-offset-2">

          <!-- START ARTICLE -->
          <div class="card article">
            <div class="card-content">
              <div class="media">
                <div class="media-center">
                  <img src="http://www.radfaces.com/images/avatars/daria-morgendorffer.jpg" class="author-image" alt="Placeholder image">
                </div>
                <div class="media-content has-text-centered">                  
                  <p class="title article-title">{{data.title.rendered}}</p>
                  <p class="subtitle is-6 article-subtitle">
                    <span>{{data.date}}</span>
                    <!-- <a href="#">@d</a> on October 7, 2017 -->
                  </p>
                </div>
              </div>

              <div v-html="data.content.rendered" class="content article-body">
              </div>
            </div>
          </div>
          <!-- END ARTICLE -->

  </div>
</template>
<script>
import axios from 'axios';

export default {
  data() {
    return {
      data: []
    };
  },
  created() {

        this.$http.get('http://thesuperblack.hol.es/wp-json/wp/v2/posts/' + this.$route.params.id)
            .then(response=> {
            //    console.log(response.data)
                this.data = response.data
            });

    // axios
    // axios.get("http://balinuxapi.000webhostapp.com/wp-json/wp/v2/posts")
    //   .then(response => {
    //     return (this.data = response.data);
    //   })
    //   .catch(err => {
    //     console.log(err);
    //   });

  }
};
</script>

<style>

</style>



